<template>
  <div class="main">
    <div class="main-header"></div>
    <div class="main-tools">
      <b-button-group vertical>
        <b-dropdown dropright text="Head" variant="warning">
          <b-dropdown-item @click="tool_click('# ')"># </b-dropdown-item>
          <b-dropdown-item @click="tool_click('## 1)')">## 1)</b-dropdown-item>
          <b-dropdown-item @click="tool_click('### 1.1)')">### 1.1)</b-dropdown-item>
          <b-dropdown-item @click="tool_click('#### 1.1.1)')">#### 1.1.1)</b-dropdown-item>
        </b-dropdown>
        <b-button variant="warning" @click="tool_click('> ')">Quote</b-button>
        <b-button variant="warning" @click="tool_click('```\n\n```')">Highlight</b-button>
        <b-button variant="warning" @click="tool_click('![img](link)')">Image</b-button>
        <b-button variant="warning" @click="tool_click('[name](link)')">Link</b-button>
        <b-dropdown dropright text="List" variant="warning">
          <b-dropdown-item @click="tool_click(' * Item 1\n * Item 2\n   * Item 2a\n   * Item 2b')">
            Unordered
          </b-dropdown-item>
          <b-dropdown-item @click="tool_click('1. Item 1\n1. Item 2\n1. Item 3\n')">
            Ordered
          </b-dropdown-item>
          <b-dropdown-item @click="tool_click(' - [x] Done\n - [ ] Not done')">Task</b-dropdown-item>
        </b-dropdown>
        <b-dropdown dropright text="Table" variant="warning">
          <b-dropdown-item @click="tool_click('Col 1 | Col 2\n-|-\nRow 1.1|Row 1.2')">
            2 Columns
          </b-dropdown-item>
          <b-dropdown-item @click="tool_click('Col 1 | Col 2 | Col 3\n-|-|-\nRow 1.1| Row 1.2 | Row 1.3')">
            3 Columns
          </b-dropdown-item>
          <b-dropdown-item @click="tool_click('Col 1 | Col 2 | Col 3 | Col 4\n-|-|-|-\nRow 1.1| Row 1.2 | Row 1.3 | Row 1.4')">
            4 Columns
          </b-dropdown-item>
        </b-dropdown>
        <b-dropdown dropright text="Emoji" variant="warning">
          <b-dropdown-item @click="tool_click(':grin:')">üòÅ</b-dropdown-item>
          <b-dropdown-item @click="tool_click(':rofl:')">ü§£</b-dropdown-item>
          <b-dropdown-item @click="tool_click(':thinking:')">ü§î</b-dropdown-item>
          <b-dropdown-item @click="tool_click(':wink:')">üòâ</b-dropdown-item>
        </b-dropdown>
      </b-button-group>
    </div>
    <div class="main-header">Markdown</div>
    <div class="main-textarea">
      <b-form-textarea
        ref="txtarea"
        v-model="text"
        no-resize
      ></b-form-textarea>
    </div>
    <div class="main-header">Preview</div>
    <div class="main-markdown">
      <VueMarkdown 
          class="form-control full-height"
          :source="text"
        ></VueMarkdown>
    </div>
  </div>
</template>

<script>
import VueMarkdown from 'vue-markdown'

export default {
  name: 'MarkDown',
  components: {
    VueMarkdown
  },
  data() {
    return {
      text: ''
    }
  },
  methods: {
    tool_click: function(symbol){
      var textarea = this.$refs.txtarea
      var cursorPosition = textarea.selectionStart
      this.text = this.text.substring(0, cursorPosition) + symbol + this.text.substring(cursorPosition)
    }
  }
}
</script>
